<template>
  <div id="index">
    <div class="wrapper">
      <div class="index-container">

        <el-card
          class="box-card"
          v-for="(item,g_index) in testData"
          :key="g_index"
        >
          <div
            slot="header"
            class="clearfix"
            style="font-size:20px"
          >
            <span><strong>{{item['title']}}</strong></span>
          </div>

          <div class="i-con">
            <p class="i-item">
              <span class="i-item-1">
                <h3>Url<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{item['url']}}</span>
            </p>

            <p class="i-item">
              <span class="i-item-1">
                <h3>Header<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{item['header']}}</span>
            </p>

            <p class="i-item">
              <span class="i-item-1">
                <h3>Method<strong>:</strong></h3>
              </span>
              <span class="i-item-2">{{item['method']}}</span>
            </p>

            <!-- 请求参数 -->
            <el-table
              :data="item['params']"
              style="width: 100%; font-size:18px"
            >
              <el-table-column
                prop="name"
                label="name"
              >
              </el-table-column>
              <el-table-column
                prop="type"
                label="type"
              >
              </el-table-column>
              <el-table-column
                prop="explain"
                label="explain"
              >
              </el-table-column>
              <el-table-column
                test-container
                prop="remark"
                label="remark"
              >
              </el-table-column>
              <el-table-column
                prop="other"
                label="other"
              >
              </el-table-column>
            </el-table>

            <!-- 测试区 -->
            <div class="test-container">
              <!-- 参数输入框 -->
              <div class="test-area">
                <div
                  class="t-area-item"
                  v-for="(item,index) in item['params']"
                  :key="index"
                >
                  <span class="t-title">{{item['name']}}<strong>:</strong></span>
                  <input
                    type="text"
                    :placeholder="item['explain']"
                    class="t-input"
                    v-model="item['value']"
                  >
                </div>
                <button
                  class="t-btn"
                  @click="startTest(g_index)"
                >测试</button>
              </div>
              <!-- 测试结果区 -->
              <pre class="t-res-area">{{item['result']}}</pre>
            </div>
          </div>

        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      testData: [],
      code: ""
    };
  },
  created() {
    this.fetchTestData();
  },

  methods: {
    processParams(_param) {
      let params = [];
      if (Array.isArray(_param)) {
        _param.forEach(_p => {
          let _ps = _p.split(":");
          params.push({
            name: _ps[0] || "",
            type: _ps[1] || "",
            explain: _ps[2] || "",
            remark: _ps[3] || "",
            other: _ps[4] || "",
            value: "" //存储输入值
          });
        });
      }
      return params;
    },

    hasKey(_v) {
      if (_v == undefined) {
        _v == false;
      }
      return _v;
    },

    fetchTestData() {
      this.$http
        .get("http://192.168.0.110:8888/doc/v1")
        .then(res => {
          let body = res.data.body;
          console.log(body);
          if (body !== null && Array.isArray(body)) {
            body.forEach(e => {
              let params = e["Param"];
              params.forEach(p => {
                this.testData.push({
                  title: this.hasKey(p["@title"]) ? p["@title"][0] : "",
                  url: this.hasKey(p["@url"]) ? p["@url"][0] : "",
                  header: this.hasKey(p["@header"]) ? p["@header"][0] : "",
                  method: this.hasKey(p["@method"]) ? p["@method"][0] : "",
                  params: this.processParams(p["@param"]),
                  result: "" //存储测试结果
                });
              });
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    startTest(_i) {
      // 处理请求参数
      let params = {};
      this.testData[_i]["params"].forEach(_p => {
        params[`${_p["name"]}`] = _p["value"];
      });
      this.$http({
        url: `http://192.168.0.110:86/${this.testData[_i]["url"]}`,
        method: this.testData[_i]["method"],
        header: {
          "Content-Type": "multipart/form-data"
        },
        params: params
      })
        .then(res => {
          this.testData[_i]["result"] = res.data;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

