import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(VueAxios, axios)
Vue.use(ElementUI);
Vue.config.productionTip = false

function setCookie(_k, _v) {
  document.cookie = `${_k}=${_v}`
}

setCookie('name00', 'zjh')
setCookie('name01', 'zjh')
setCookie('name02', 'zjh')

function getCookie(_k) {
  let cookies = document.cookie.split("; ")
  let cookiesLen = cookies.length
  let returnVal = ""
  for (let i = 0; i < cookiesLen; i++) {
    let vals = cookies[i].split("=")
    if (vals[0] === _k) {
      returnVal = vals[1]
      break
    }
  }
  return returnVal
}
console.log(getCookie('name00'));
console.log(getCookie('name01'));
console.log(getCookie('name02'));
new Vue({
  render: h => h(App),
}).$mount('#app')
